.contactCard {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  border: 1px solid #999999;
  border-radius: 4px;
  padding: 10px;
  height: 40px;
}

.contactCard span:nth-child(1) {
  flex: 0 0 48%;
}

.contactCard span:nth-child(2) {
  flex: 0 0 28%;
}

.btn {
  flex: 0 0 20%;
  height: 30px;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 700;
  background-color: rgba(65, 65, 175, 1);
  color: #fff;
}

.btn:focus,
.btn:active {
  box-shadow: none;
  outline: none;
}
.form {
  padding: 10px;
  border-radius: 4px;
  box-shadow: 0px 2px 3px 1px rgba(71, 71, 108, 0.3);
  margin-bottom: 25px;
}

.label {
  display: block;
  margin-bottom: 20px;
}

.label p {
  margin: 0 0 10px 0;
}

.input {
  border: 0;
  padding: 0 10px;
  display: block;
  width: 95%;
  height: 40px;
  border: 1px solid #c0c0c0;
  border-radius: 4px;
  font-size: 16px;
}

.input:focus {
  border: 1px solid#4040af;
  box-shadow: none;
  outline: none;
}

.btn {
  display: block;
  width: 100%;
  height: 40px;
  border-radius: 4px;
  border: none;
  box-shadow: 0px 2px 3px 1px rgba(71, 71, 108, 0.3);
  font-size: 18px;
  font-weight: 700;
  margin-top: 10px;
  background-color: rgba(65, 65, 175, 1);
  color: #fff;
}

.btn:hover {
  background-color: rgba(65, 65, 175, 0.9);
  transition: 0.5s;
}

.btn:focus,
.btn:active {
  box-shadow: none;
  outline: none;
}

.contactButt {
  width: 30px;
  height: 30px;
  background-color: rgb(223, 41, 41);
  color: #fff;
  border: none;
  border-radius: 4px;
}
